import 'bootstrap';
import Swup from 'swup';
import SwupOverlayTheme from '@swup/overlay-theme';
import AOS from 'aos';
import smoothscroll from 'smoothscroll-polyfill';
import './styles.scss';

// swup
const swup = new Swup({
    plugins: [new SwupOverlayTheme({
        color: '#F5F5F5'
        // color: 'linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);'
    })]
});

// copyright get current year
const copyright = () => {
    const d = new Date();
    document.querySelector("#copyright").innerHTML = "&copy; Warren Yen " + d.getFullYear();
}

// AOS scroll animation
const aosAnimate = () => {
    AOS.init({
        offset: 200,
        duration: 600,
        easing: 'ease-in-out-sine',
        delay: 0
    });
}

// scroll to top
const scrollTopButton = () => {
    let scrollBtn = document.getElementById("scrollTop");
    scrollBtn.addEventListener("click", scrollTop);
    function scrollTop() {
        document.querySelector('#swup').scrollIntoView({ behavior: 'smooth' });
    }  
};

// nav color change on scroll
const navScroll = () => {
    const myNav = document.getElementById('myNav');
    window.scrollY >= 70
    window.onscroll = function () { 
        if (window.scrollY >= 70) {
            myNav.classList.add("bg-white");
        } 
        else {
            myNav.classList.remove("bg-white");
        }
    }
};

// init all functions
copyright();
aosAnimate();
scrollTopButton();
smoothscroll.polyfill();
navScroll();

// js reload on view change
swup.on('contentReplaced', () => {
    copyright();
    aosAnimate();
    scrollTopButton();
    smoothscroll.polyfill();
    navScroll();
    scrollTo(0, 0);
});
